import { z } from 'zod';
import api from '@/lib/axios';
import { useAppMutation } from '@/lib/react-query/hooks';
import { PlanogramCreateRequest, Planogram } from '@/lib/generated/api-schemas';

type CreatePlanogramInput = z.infer<typeof PlanogramCreateRequest>;
type PlanogramResponse = z.infer<typeof Planogram>;

export function useCreatePlanogramMutation() {
  return useAppMutation<PlanogramResponse, CreatePlanogramInput>(
    async (input) => {
      const validatedInput = PlanogramCreateRequest.parse(input);
      const response = await api.post('/planograms/', validatedInput);
      return Planogram.parse(response.data);
    },
    {
      successMessage: 'Planogram created successfully',
      errorMessage: 'Failed to create planogram',
      invalidateQueries: [['planograms'], ['projects']],
    }
  );
}

