import { useQueryClient } from '@tanstack/react-query';
import { z } from 'zod';
import api from '@/lib/axios';
import { useAppMutation } from '@/lib/react-query/hooks';
import { PlanogramUpdateRequest, PlanogramUpdate } from '@/lib/generated/api-schemas';
import type { LayoutItem } from '../types';

type UpdatePlanogramInput = z.infer<typeof PlanogramUpdateRequest>;

interface UpdatePlanogramVariables extends UpdatePlanogramInput {
  slug: string;
  layout?: Record<number, LayoutItem[]>;
}

type PlanogramResponse = z.infer<typeof PlanogramUpdate>;

export function useUpdatePlanogramMutation() {
  const queryClient = useQueryClient();

  return useAppMutation<PlanogramResponse, UpdatePlanogramVariables>(
    async (variables) => {
      const { slug, ...data } = variables;
      const validatedInput = PlanogramUpdateRequest.parse(data);
      const response = await api.put(`/planograms/${slug}/`, validatedInput);
      return PlanogramUpdate.parse(response.data);
    },
    {
      successMessage: 'Planogram updated successfully',
      errorMessage: 'Failed to update planogram',
      invalidateQueries: [['planograms']],
      onSuccess: (data) => {
        queryClient.setQueryData(['planograms', data.name], data);
        queryClient.invalidateQueries({ queryKey: ['planograms', data.name] });
      },
    }
  );
}

