import { z } from 'zod';
import api from '@/lib/axios';
import { usePaginatedQuery } from '@/lib/react-query/hooks';
import { PlanogramList, PaginatedPlanogramListList } from '@/lib/generated/api-schemas';

type PlanogramType = z.infer<typeof PlanogramList>;

export function usePlanogramsQuery() {
  return usePaginatedQuery<PlanogramType>(
    ['planograms'],
    async () => {
      const response = await api.get('/planograms/');
      return PaginatedPlanogramListList.parse(response.data);
    }
  );
}

